
import './App.css';
import Home from './Home';

import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';
import Golink from './Golink';


function App() {
  return (
    <div className="app">
      <Router>
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/l/:code' component={Golink}/>
          </Switch>
      </Router>
    </div>
  );
}

export default App;
