import React, { useState } from "react";
import {db} from './firebase';
import {v4 as uuidv4} from 'uuid';
import './Home.css';

function Home(){
    const [url, setUrl] = useState("");

    const handleFormSubmit= async (e)=>{
        e.preventDefault();
        if(url==""){
            alert("Please Enter Valid URL.....!");
            
        }
        else{
            let code=uuidv4();
        await db.collection('urls').add(
            {
                url:url,
                code:code,
            }
        );
        alert("This is Your Url - http://localhost:3000/l/"+code);
        }
    };
    return (<>
        <center>
        <form className="form_all" onSubmit={handleFormSubmit}>
        <div className="input_coontainer">
            <input id="txt" type="text" value={url} onChange={e=>setUrl(e.target.value)} placeholder="Enter your URL Here..."/>
            <input id="btn" type="submit" value="Shortern The Url"/>
        </div>
        </form>
        </center>

    </>);
}

export default Home;